import * as Yup from "yup";
import { useEffect, useMemo, useState } from "react";
import { useSnackbar } from "notistack";
import { useNavigate } from "react-router-dom";
// form
import { useForm } from "react-hook-form";
import { yupResolver } from "@hookform/resolvers/yup";
// @mui
import { LoadingButton } from "@mui/lab";
import { Box, Card, Grid, Stack, Typography } from "@mui/material";
// routes
import { PATH_DASHBOARD } from "../../../routes/paths";
// components
import {
  FormProvider,
  RHFSelect,
  RHFTextField,
  RHFPhoneField,
} from "../../../components/hook-form";
import { useDispatch, useSelector } from "../../../redux/store";
import {
  getAllCountries,
  autoDetectOperator,
  fxRate,
  getOperatorSuccess,
  getFxRateSuccess,
  startLoading,
  stopLoading,
  sendTopUp,
  resetState,
} from "../../../redux/slices/sendtopup";
import useIsMountedRef from "../../../hooks/useIsMountedRef";
import extractErrorMessage from "../../../utils/error";
import { useTranslation } from "react-i18next";
// ----------------------------------------------------------------------

export default function SendTopUpForm() {
  const navigate = useNavigate();

  const { t } = useTranslation();

  const { enqueueSnackbar } = useSnackbar();
  const [chooseOperator, setChooseOperator] = useState(false);
  const [operators, setOperators] = useState([]);

  const TopUpSchema = Yup.object().shape({
    phone: Yup.string().required(
      t("field.required", {
        obj: t("field.phone"),
      })
    ),
    country: Yup.string().required(
      t("field.required", {
        obj: t("field.country"),
      })
    ),
    operator_id: Yup.number().required(
      t("field.required", {
        obj: t("field.operator"),
      })
    ),
    amount: Yup.number().required(
      t("field.required", {
        obj: t("field.amount"),
      })
    ),
  });

  const defaultValues = useMemo(
    () => ({
      phone: "",
      country: "HT",
      operator_id: "",
      amount: 0,
    }),
    // eslint-disable-next-line react-hooks/exhaustive-deps
    []
  );

  const methods = useForm({
    resolver: yupResolver(TopUpSchema),
    defaultValues,
  });

  const dispatch = useDispatch();

  const isMountedRef = useIsMountedRef();

  const {
    reset,
    watch,
    setError,
    setValue,
    handleSubmit,
    formState: { isSubmitting },
  } = methods;

  const values = watch();

  const handleAutoDetect = async () => {
    try {
      dispatch(startLoading());
      const res = await autoDetectOperator(values?.phone, values?.country);
      dispatch(getOperatorSuccess(res?.operator));
      setValue("operator_id", res?.operator?.operatorId);
    } catch (err) {
      // console.log(err);
      dispatch(stopLoading());
      if (err.response.data?.found === "FOUND") {
        setChooseOperator(true);
        setOperators(err.response.data?.operators);
      } else {
        const err_ = extractErrorMessage(err);
        enqueueSnackbar(err_, {
          variant: "error",
        });
      }
    }
  };

  const handleAutoFxRate = async () => {
    try {
      dispatch(startLoading());
      const res = await fxRate(values?.amount, operator_set?.operatorId);
      dispatch(getFxRateSuccess(res?.rate));
    } catch (err) {
      // console.log(err);
      dispatch(stopLoading());
      const err_ = extractErrorMessage(err);
      enqueueSnackbar(err_, {
        variant: "error",
      });
    }
  };

  const onSubmit = async () => {
    try {
      const result = await sendTopUp(values);
      reset();
      enqueueSnackbar(t("success.CREATE"));
      dispatch(resetState());
      navigate(PATH_DASHBOARD.ticket.view(result?.id));
    } catch (error) {
      // console.error(error);

      const errorMessage = extractErrorMessage(error);

      enqueueSnackbar(t(`error.${errorMessage}`), {
        variant: "error",
      });
      if (isMountedRef.current) {
        if (error.response?.data?.phone) {
          setError("phone", { ...error, message: error.response?.data?.phone });
        }
      }
    } finally {
      dispatch(stopLoading());
      methods.clearErrors("afterSubmit");
    }
  };

  const { countries, operator_set, fx_rate, loading_operator } = useSelector(
    (state) => state.sendtopup
  );

  useEffect(() => {
    dispatch(getAllCountries());
  }, [dispatch]);

  return (
    <FormProvider methods={methods} onSubmit={handleSubmit(onSubmit)}>
      <Grid container spacing={3}>
        <Grid item xs={12} md={8}>
          <Card sx={{ p: 3 }}>
            <Box
              sx={{
                display: "grid",
                columnGap: 2,
                rowGap: 3,
                gridTemplateColumns: {
                  xs: "repeat(1, 1fr)",
                  sm: "repeat(2, 1fr)",
                },
              }}
            >
              <RHFSelect
                name="country"
                label={t("field.country")}
                placeholder={t("field.country")}
              >
                <option value="" />
                {countries?.countries?.map((option) => (
                  <option key={option.isoName} value={option.isoName}>
                    {option.name}
                  </option>
                ))}
              </RHFSelect>
              <RHFPhoneField
                name="phone"
                label={t("field.phone")}
                onBlur={handleAutoDetect}
              />
            </Box>
            <Grid
              item
              xs={12}
              sx={{
                py: 1,
                display: "flex",
                justifyContent: "center",
                alignItems: "center",
                flexDirection: "column",
              }}
            >
              {chooseOperator && (
                <Box display="flex" flexWrap="wrap" gap={2}>
                  {operators.map((operator) => (
                    <Card
                      key={operator.operatorId}
                      onClick={() => {
                        setValue("operator_id", operator.operatorId);
                        dispatch(getOperatorSuccess(operator));
                      }}
                      sx={{
                        cursor: "pointer",
                        border:
                          values?.operator_id === operator.operatorId
                            ? "2px solid #007bff"
                            : "1px solid #ddd",
                        p: 2,
                      }}
                    >
                      <img
                        src={operator.logoUrls?.[0]}
                        alt={operator.name}
                        style={{
                          width: "80px",
                          height: "auto",
                          objectFit: "contain",
                        }}
                      />
                      <Box mt={1}>{operator.name}</Box>
                    </Card>
                  ))}
                </Box>
              )}
              {!chooseOperator && operator_set && (
                <Card
                  sx={{
                    cursor: "pointer",
                    border: "2px solid #007bff",
                    p: 2,
                  }}
                >
                  <img
                    src={operator_set.logoUrls?.[0]}
                    alt={operator_set.name}
                    style={{
                      width: "80px",
                      height: "auto",
                      objectFit: "contain",
                    }}
                  />
                  <Box mt={1}>{operator_set.name}</Box>
                </Card>
              )}
            </Grid>
            <Box
              sx={{
                display: "grid",
                columnGap: 2,
                rowGap: 3,
                gridTemplateColumns: {
                  xs: "repeat(1, 1fr)",
                  sm: "repeat(2, 1fr)",
                },
              }}
            >
              <RHFTextField
                name="amount"
                label={`${t("field.montant")} USD`}
                onBlur={handleAutoFxRate}
              />
              <Box sx={{ backgroundColor: "#007bff", borderRadius: 0.8 }}>
                <Typography sx={{ pl: 1 }}>Total:</Typography>
                <Typography
                  variant="h5"
                  sx={{ pl: 1, color: "white", fontWeight: "700" }}
                >
                  {fx_rate?.fxRate || 0} {fx_rate?.currencyCode}
                </Typography>
              </Box>
            </Box>

            <Stack alignItems="flex-end" sx={{ mt: 3 }}>
              <LoadingButton
                type="submit"
                variant="contained"
                loading={isSubmitting || loading_operator}
              >
                {t("p_sendtopup.sendtopup")}
              </LoadingButton>
            </Stack>
          </Card>
        </Grid>
      </Grid>
    </FormProvider>
  );
}
